.question-info {
    display: flex;
    width: 250px;
    margin: auto;
    flex-direction: column;
    align-items: center;
    background: #fff;
    margin-bottom: 20px;
    padding: 20px 0;
    border-radius: 5px;
    box-shadow: 0 0 8px rgba(#333, 0.3);
    color: #444;

    img {
        width: 100px;
        height: 100px;
        border-radius: 50%;
        padding: 1px;
        border: 1px solid #ccc;
        box-shadow: 0 0 4px rgba(#2980b9, 0.4);
    }

    span {
        color: #2980b9;
    }

    h4 {
        margin: 4px 0 10px;
    }

    p {
        color: #777;
    }

    .poll-btn {
        width: 90%;
        padding: 5px 0;
        color: #fff;
        border: 1px solid #2980b9;
        background: #2980b9;
        border-radius: 5px;
        cursor: pointer;
        margin-top: 10px;
        transition: 0.2s ease-in-out;
        text-decoration: none;

        &:hover {
            background: #fff;
            color: #2980b9;
        }
    }
}
